.TextField {
  input {
    width: 320px;
    height: 44px;
    border: 1px solid rgba(31, 32, 65, 0.25);
    box-sizing: border-box;
    border-radius: 4px;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: rgba(31, 32, 65, 0.75);
    padding-left: 12px;
    &:focus,
    &:hover {
      border: 1px solid rgba(31, 32, 65, 0.5);
      outline-width: 0;
    }
  }
}
